<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

// ---- 參數設定 -------------------------------------
const scrollValue = ref(0);
const navScrolled = ref(false);

// 右側按鈕滑動監控
const showButtons = ref(false);

const main = ref(null);
const ucg = ref(null);

// 滑動監控
const handleScroll = () => {
  scrollValue.value = main.value.scrollTop;
  navScrolled.value = scrollValue.value > 300;
  showButtons.value = scrollValue.value > 300;
};

// 滑動
const scrollToMain = () => {
  if (main.value) {
    const topPos = 0;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

const scrollToUCG = () => {
  if (main.value) {
    const topPos = ucg.value.getBoundingClientRect().top;
    main.value.scrollTo({
      top: topPos,
      behavior: "smooth",
    });
  }
};

const initializePage = () => {
  logoMarqueeAction();
};

onMounted(() => {
  initializePage();
});

onUnmounted(() => {
  window.removeEventListener("scroll", handleScroll);
});

// ==== 過場跑馬燈動畫 ==============================
const logoMarquee = ref(null);
// 獲取跑馬燈容器的寬度

const logoMarqueeAction = () => {
  // 用 GASP 做出無縫的跑馬燈
  const animationDistance = logoMarquee.value.offsetWidth / 2;
  gsap.to(".logoMarquee", {
    x: -animationDistance,
    duration: 60,
    ease: "none",
    repeat: -1,
  });
};
</script>

